<template>
  <div style="width:100%">
     <el-row class="toolbar">
       <el-button type="primary" :disabled="type==0"  @click="newAdd"  style="float:right;margin-bottom:10px">新建</el-button>
     </el-row>
   <el-container>
          <el-form ref="form1" :inline="true" class="toolbar"  label-position="right" >
            <el-form-item >
              <el-tree
                :data="data"
                :props="defaultProps"
                accordion
                @node-click="handleNodeClick">
              </el-tree>
            </el-form-item>
        </el-form>
        <el-table :data="tableData" border class="ml15" >
          <el-table-column header-align="center" align="center" label="序号"  min-width="50">
            <template slot-scope="scope">
                <span>{{ scope.row.id }}</span>
              </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" label="操作"  min-width="150">
            <template slot-scope="scope">
              <!-- <el-button type="danger" size="mini" @click = "add_query(scope.row, scope.$index)">添加</el-button> -->
              <el-button type="danger" size="mini" @click = "del_query(scope.row, scope.$index)">删除</el-button>
              <el-button type="primary" size="mini" @click = "edit_query(scope.row, scope.$index)">编辑</el-button>
            </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" label="类型名称"  >
            <template slot-scope="scope">
              <span>{{scope.row.type_item_name}}</span>
            </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" label="创建时间"  >
            <template slot-scope="scope">
              <span>{{scope.row.create_time | time}}</span>
            </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" label="创建人"  >
            <template slot-scope="scope">
              <span>{{scope.row.op_username}}</span>
            </template>
          </el-table-column>
          <el-table-column header-align="center" align="center" label="状态"  >
              <template slot-scope="scope">
                {{scope.row.status==1?'启用':'停用'}}
              </template>
          </el-table-column>
        </el-table>
      <!-- 新建知识 -->
      <el-dialog :title="title1" :visible.sync="dialogFormOneVisible">
        <el-form label-width="120px" label-position="left" ref="form1" :model="form_one_data" :rules="rules1" >
          <el-form-item label="类型名称" class="ml15" prop="type_item_name">
            <el-col :span="12">
              <el-input v-model="form_one_data.type_item_name"></el-input>
            </el-col>
          </el-form-item>
        <el-form-item label="值" class="ml15" prop="type_item_value">
            <el-col :span="12">
              <el-input v-model="form_one_data.type_item_value"></el-input>
            </el-col>
        </el-form-item>
          <el-form-item label="状态" class="ml15" prop="status">
          <el-col :span="12">
            <el-radio v-model="form_one_data.status" label="1" >启用</el-radio>
            <el-radio v-model="form_one_data.status" label="2">停用</el-radio>
          </el-col>
        </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormOneVisible = false">取 消</el-button>
          <el-button  type="primary" @click="handle_add_query">确 定</el-button>
        </div>
      </el-dialog>
   </el-container>
  </div>
</template>
<script>
  import { koneledgeTree,koneledgeList,deldictsData,addKnowledge,detailDictsData} from '@/api/api'
  import { tel_num } from '@/assets/utils/validate';
  export default {
    data() {
      return {
        title1:'新建',
        dialogFormOneVisible:false,
        data: [],
        defaultProps: {
            children: 'children',
            label: 'label',
            value:'value',
        },
        type:0,
        type_name:'',
        tableData:[],
        form_one_data:{
            'type':'',
            'type_name':'',
            'type_item_name':'',
            'type_item_value':'',
            'status':1,
        },
        rules1: {
            type_item_name: [
                { required: true, message: '请输入车辆品牌', trigger: 'blur' },
            ],
            type_item_value: [
                { required: true, message: '请输入车辆类型', trigger: 'blur' },
            ],
        },
      };
    },
    mounted(){
        this.query();
    },
    methods: {
        query(){
            koneledgeTree({}).then((res) => {
                this.data = res.data.data;
                //this.queryStr.total = Number(res.data.data.total);
            });
        },
        add_query(val,index){
            this.form_one_data = {};
            this.type=val.type;
            this.form_one_data.type = val.type;
            this.title1='新建';
            this.form_one_data.status ='1';
            this.form_one_data.type_name = val.type_name;
            this.dialogFormOneVisible = true;
        },
        handle_add_query(){

            this.$refs['form1'].validate((valid) => {
                if (valid) {
                    let params = this.form_one_data;
                    let user = localStorage.getItem('user');
                    if (user && this.title1=='新建') {
                        user = JSON.parse(user);
                        params.op_username = user.admin_username || '';
                        params.op_userid = user.admin_userid || '';
                    }
                    addKnowledge(params).then((res) => {
                        this.$message.success('操作成功!');
                        this.dialogFormOneVisible=false;
                        koneledgeList({'type':this.type}).then((res) => {
                            this.tableData = res.data.data;
                            //console.log(this.data)
                        });
                    });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        
        },
        edit_query(val,index){
            this.title1 = '编辑';
            let id = val.id;
            let params = {
                'id':id,
            }
            detailDictsData(params).then((res) => {
                this.form_one_data = res.data.data;
                this.dialogFormOneVisible=true;
            });
        },
        del_query(val,index){ //删除
            let id = val.id;
            let params = {
                'id':id,
            }
            deldictsData(params).then((res) => {
                this.$message.success('【删除】操作成功!');
                console.log(this.type)
                    koneledgeList({'type':this.type}).then((res) => {
                        console.log( res.data.data)
                    this.tableData = res.data.data;
                    //console.log(this.data)
                    });
            });
        },
        handleNodeClick(data) {
            this.type = data.value;
            this.type_name=data.label;
            let params = {
            'type':this.type,
            }
            koneledgeList(params).then((res) => {
           
                this.tableData = res.data.data;
                //console.log(this.data)
            });
        },
        newAdd(){
            this.form_one_data = {};
            this.form_one_data.type = this.type;
            this.title1='新建';
            this.form_one_data.status ='1';
            this.form_one_data.type_name = this.type_name;
            this.dialogFormOneVisible = true;
        }
    }
  }    
</script>
<style scoped lang="scss">
  .el-radio-group {
		display: -webkit-flex;
		flex-wrap: wrap;
		label {
			flex-basis: 25%;
			margin-left:0 ;
			line-height: 30px;
		}
	}
</style>

<style>
.select  .el-input__inner{
    width:203px;
  }
</style>





